import React, { Component } from "react";
import { connect } from "react-redux";
import { Button } from "react-bootstrap";
import { dateFormat } from "../../utils"; 
import { paginate } from '../../../../../CommonComponents/Paginate'; 
import Paggination from "../common/popup/paggination";
import PopupSearch from "../common/popup/popupSearch";
import ConnectButton from "../common/connectButton";
import PopupListSkeleton from "../skeleton/popupListSkeleton";
import { kFormatter } from "../../../../../../utils";
import PostMedia from "../common/postMedia";
import NetworkIcon from "../common/networkIcon";
import AuthorInfo from "../../content/components/authorInfo";
import Sentiment from "../../../Dashboard/components/common/sentiment";
import DataNotFound from "../common/DataNotFound";

class PostPopup extends Component {
  state = {
    fetchRecord: null,
    isLoading: true,
    total: 0,
    limit: 10,
    offset: 0,
    paginateData: {},
    currentPage: 1,
    maxPage: 10,
    username: null
  }

  componentDidMount() {
     const { total } = this.props;
     const { limit, currentPage, maxPage } = this.state;
      if (total) {
        this.setState({ total: total, paginateData: paginate(total, currentPage, limit, maxPage) }, () => this.onLoadFilterData())
      }
   }


  onLoadFilterData = (isSearch=0) => { 
    
    const { analyticwall, total, moreData } = this.props;
    const { selectedWallFeed, dateFilter, selectedFeed } = analyticwall;
    const { currentPage, limit, offset, username, maxPage } = this.state;

    let wallData = selectedWallFeed?.wall?.id;
    let startDate = dateFilter.startDate;
    let endDate = dateFilter.endDate;

    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
      selectedFeed.map((fItem) => {
        feedIDs.push(fItem.value);
      })
    }

    this.setState({ isLoading: true })

      // type Popup module
       moreData(wallData, dateFormat(startDate), dateFormat(endDate), feedIDs.join(), isSearch ? username && username.length > 0 ? total : limit  :  offset, isSearch ? username.length > 0 ? 0 : offset : limit, 0, username)
      .then((response) => {
  
        const { responseData } = response.data;

        if(!isSearch) this.setState({ fetchRecord: responseData.postData, isLoading: false })
          else
        this.setState({ total: responseData.totalCount, paginateData: paginate(responseData.totalCount, currentPage, limit, maxPage), fetchRecord: responseData.postData, isLoading: false })
    })
      .catch((error) => {
        console.error("LAYOUT ISSUE", error)
      });
   }   


  managePagging=(pageClick,currentPage=0,total=0,page=0,limit=0,maxPage=0,offset=0)=>{
      if(pageClick==1)
          this.setState({ currentPage: currentPage });
          else if(pageClick==2)
          this.setState(
            { paginateData: paginate(total, page, limit, maxPage), offset: offset },
            () => this.onLoadFilterData()
        );
         else if(pageClick==3)
          this.setState(
            {
                currentPage: page,
                paginateData: paginate(total, page, limit, maxPage),
                offset: offset,
            },
            () => this.onLoadFilterData()
            );
        else if(pageClick==4)
        this.setState(
          {
              limit: limit,
              paginateData: paginate(total, currentPage, limit, maxPage),
              offset: offset,
          },
          () => this.onLoadFilterData()
        );
     }


  updateSearchText = (value) => {
    this.setState({ username: value, currentPage: 1 }, () => this.onLoadFilterData(1))
  }
  
  render() {
    const {  analyticsStatus, isPdfDownloading, maxPage  } = this.props;
    const { isLoading, fetchRecord, total, limit, paginateData, currentPage } = this.state;


    return (
      <div className="influential_contributors_custom_modal px-5">
        <PopupSearch isPdfDownloading={isPdfDownloading} analyticsStatus={analyticsStatus} updateSearchText={this.updateSearchText}/>
        <div className="influential-data-list sw_theme ">
          {
            isLoading ? <PopupListSkeleton /> : (fetchRecord && fetchRecord.length > 0) ? fetchRecord.map((item, mIndex) => <div className="card mb-3">
            <div className="p-3 users_row">
              <div className="row align-items-start">
 
              {![1].includes(item.type) ?
                  <div className="col-lg-2">
                    <div className="symbol_item position-relative">
                        <div className="symbol symbol-170 symbol-2by2 flex-shrink-0">
                            <Button type="button" className="border-0 p-0 bg-transparent rounded">
                             <PostMedia
                                mediaType={item.type }
                                postFile={item.postFile}
                                postId={"postPrimaryId"}
                                className={"img-fluid symbol-label no-repeat-fit-background rounded"}
                              />
                            </Button>
                        </div>
                    </div>
                  </div> : null }

                  <div className={`col-lg-${![1].includes(item.type) ? '10' : '12'}`}>
                    <div className="w-100">
                      <div className="d-flex flex-grow-1 w-100 mb-3 justify-content-between align-items-center">
                          <div className="d-flex justify-content-between align-items-center">
                            <div className="d-flex align-items-center"> 
                               <AuthorInfo link={item.link} topperfoming={1} picture={item.authorImage}  authorUsername={item.authorUsername} authorName={item.authorName}  date={item.created} /> 
                            </div>
                            <div className="list_category_icon cursor-pointer d-flex">
                                 <NetworkIcon icon={item.network.icon} size={'font-size-h2'}/>
                            </div>
                          </div>
                          <ConnectButton link={item.link} network={item.network.id} />
                      </div>
                      <div className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-3">{item.content}</div>
                    </div>
                    <div className="row align-items-center mt-5">


                   { item.recommendationScore ?
                      <div className="col-md-6 col-12">
                        <div className="d-flex align-items-center justify-content-between py-2">
                            <span className="text-secondary font-weight-bold font-size-md w-50">AI Recommendation</span>
                            <p className="text-dark fw-bold mb-0 d-flex align-items-center w-50"><span className="bulits bg-success d-block rounded-circle size-12 me-2"></span>  Recommendation ({item.recommendationScore})
                            </p>
                        </div>
                      </div> : null }

                    { item.sentiment ?  
                      <div className="col-md-6 col-12">
                        <div className="d-flex align-items-center justify-content-between py-2">
                            <span className="text-secondary font-weight-bold font-size-md w-50">Sentiment</span>
                            <div className="w-50">
                               <Sentiment sentiment={item.sentiment} />
                            </div>
                        </div>
                      </div> : null}

 
                     { item.postPerformance ?   
                      <div className="col-md-6 col-12">
                        <div className="d-flex align-items-center justify-content-between py-2">
                            <span className="text-secondary font-weight-bold font-size-md w-50">Impressions</span>
                            <p className="text-dark fw-bold mb-0 d-flex w-50">
                              {kFormatter(Math.round(item.postPerformance))} 
                            </p>
                        </div>
                      </div> : null }

                     { item.feedName ? 
                      <div className="col-md-6 col-12">
                        <div className="d-flex align-items-center justify-content-between py-2">
                            <span className="text-secondary font-weight-bold font-size-md w-50"> Feed Name</span>
                                <p className="text-dark fw-bold mb-0 d-flex w-50">{`#${item.feedName}`}</p>
                        </div>
                      </div> : null }

                      { item.postClicks ?
                      <div className="col-md-6 col-12">
                        <div className="d-flex align-items-center justify-content-between py-2">
                            <span className="text-secondary font-weight-bold font-size-md w-50">Post Clicks</span>
                                <p className="text-dark fw-bold mb-0 d-flex w-50">
                                  { kFormatter(item.postClicks)}
                                </p>
                        </div>
                      </div> : null }

                    </div>
                  </div>
              </div>
            </div>
           </div>
            ) : <DataNotFound />
          }

        </div>
          <Paggination isLoading={isLoading} total={total} paginateData={paginateData} maxPage={maxPage} currentPage={currentPage} limit={limit}  managePagging={this.managePagging} />
       </div>
    );
  }
}
const mapStateToProps = (state) => {    
  return {
    analyticwall: state.analyticwall,
  }
}

export default connect(mapStateToProps)(PostPopup);